// Adaptive-Responsive Design
.container {
  margin: 0 auto;
  width: 100%;
  min-width: $mobile-width;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 30px;
  }

  @media (min-width: $desktop-width) {
    max-width: $max-desktop-width;
    padding: 0 40px;
  }

  &--fullscreen {
    min-height: 100vh;
   display: flex;
    flex-direction: column;
  }
}
